/**
 * 模拟演示
 */
import React from "react";

class simDemostration extends React.Component {
  render() {
    return (
      <div>
        <section className="slice sct-color-2 b-xs-top ">
          <div className="container">
            <div className="row cols-xs-space cols-sm-space cols-md-space">
              <div className="col-lg-5">
                <h2 className="heading heading-3 strong-300 text-normal">
                  水质分析
                </h2>
                <p className="mt-4">
                  采集水质水位、浊度、余氯、PH等指标，可分时间段，监测指标，监测点不同角度，不同维度观测，
                  使决策者实时了解水质情况。各个指标通过安置的传感器通过通讯模块上传数据。通过此模块也可
                  详细了解到传感器的工作状态。
                </p>
                <span className="space-xs-xl" />
              </div>
              <div className="col-lg-1" />
              <div className="col-lg-6 ml-lg-auto">
                <div className="block block-image-holder z-depth-3">
                  <div className="block-image">
                    <img alt="img"
                      src={require("@/assets/images/case/demo11.png")}
                      style={{ height: "300px", width: "628px" }}
                    />
                  </div>
                </div>
              </div>
            </div>
            <span className="space-xs-xl" />
            <div className="row cols-xs-space cols-sm-space cols-md-space">
              <div className="col-lg-6">
                <div className="block block-image-holder z-depth-3">
                  <div className="block-image">
                    <img alt="img"
                      src={require("@/assets/images/case/demo33.png")}
                      style={{ height: "300px", width: "608px" }}
                    />
                  </div>
                </div>
              </div>
              <div className="col-lg-1" />
              <div className="col-lg-5 ml-lg-auto">
                <h2 className="heading heading-3 strong-300 text-normal">
                  地表积水
                </h2>
                <p className="mt-4 mb-4">
                  通过暴雨洪水模型模拟降雨，使用SWMM内核算法结合地面高程，模拟降雨过后地面积水深度。
                  积水算法通过多方监测，可最大程度地保证理想情况下计算结果的准确性。对于不同地区水文情况
                  不尽相同，可对结果酌情增减。
                </p>
                <span className="space-xs-xl" />
              </div>
            </div>
          </div>
        </section>
        <section className="slice sct-color-1  b-xs-top">
          <div className="container">
            <div className="row cols-xs-space cols-sm-space cols-md-space">
              <div className="col-lg-5">
                <h2 className="heading heading-3 strong-300 text-normal">
                  暴雨模拟
                </h2>
                <p className="mt-4">
                  通过暴雨洪水模型作为模拟降雨的算法公式，不同地区可采用不同参数，平台内置不同地区的地方参数可供选择。
                  模拟出特定时间段的降雨数据呈正态分布，可导出使用。
                </p>
                <span className="space-xs-xl" />
              </div>
              <div className="col-lg-1" />
              <div className="col-lg-6 ml-lg-auto">
                <div className="block block-image-holder z-depth-3">
                  <div className="block-image">
                    <img alt="img"
                      src={require("@/assets/images/case/demo22.png")}
                      style={{ height: "300px" }}
                    />
                  </div>
                </div>
              </div>
            </div>
            <span className="space-xs-xl" />
            <div className="row cols-xs-space cols-sm-space cols-md-space">
              <div className="col-lg-6">
                <div className="block block-image-holder z-depth-3">
                  <div className="block-image">
                    <img alt="img"
                      src={require("@/assets/images/case/demo44.png")}
                      style={{ height: "300px" }}
                    />
                  </div>
                </div>
              </div>
              <div className="col-lg-1" />
              <div className="col-lg-5 ml-lg-auto">
                <h2 className="heading heading-3 strong-300 text-normal">
                  管网分析
                </h2>
                <p className="mt-4 mb-4">
                  观测降雨模拟后管网充满度变化，可选择特定充满度进行分析。使用GIS技术，实现管网的最短路径分析、
                  服务范围分析、排水设施分析等，方便用户查询管网具体信息以及险情出现时及时发现具体的疏导路线与附近排
                  水设施。
                </p>
                <span className="space-xs-xl" />
              </div>
            </div>
          </div>
        </section>
        <section className="slice sct-color-2 b-xs-top ">
          <div className="container">
            <div className="row cols-xs-space cols-sm-space cols-md-space">
              <div className="col-lg-5">
                <h2 className="heading heading-3 strong-300 text-normal">
                  综合分析
                </h2>
                <p className="mt-4">
                  管道模型的属性信息查询以及选择特定形状进行空间查询。采用GIS方法，对管道模型的积水点信息进行归纳整理，
                  可采用多边形、圆形等查询方式，方便实用；也可针对特定属性，比如水位值进行查询，可准确、迅速查找出特定
                  积水点和管段。
                </p>
                <span className="space-xs-xl" />
              </div>
              <div className="col-lg-1" />
              <div className="col-lg-6 ml-lg-auto">
                <div className="block block-image-holder z-depth-3">
                  <div className="block-image">
                    <img alt="img"
                      src={require("@/assets/images/case/demo55.png")}
                      style={{ height: "300px" }}
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}

export default simDemostration;



