import React from "react";

export default class character extends React.Component{
    render() {
      return (
  
        <section className="slice sct-color-1 b-xs-top">
          <div className="container">
            <div className="section-title section-title--style-1 text-center mb-2">
              <h3 className="section-title-inner text-normal">
                <span>6大特点</span>
              </h3>
              <span className="section-title-delimiter clearfix d-none" />
            </div>
            <span className="clearfix" />
            <span className="space-xs-xl" />
            <div className="row-wrapper">
              <div className="row">
                <div className="col-lg-6 col-md-12 col-sm-12 col-12">
                  <div className="feature feature--boxed-border feature--bg-1">
                    <div className="icon-block icon-block--style-1-v1">
                      <div className="block-icon">
                        <i className="icon-number">1</i>
                      </div>
                      <div className="block-content">
                        <h3 className="heading heading-5 strong-500 mb-2">水质参数实时监测</h3>
                        <p className="mb-2">
                          利用传感器及通讯模块交互技术，对各个水质监测点实时监控，风险评估；
                        </p>
                        <p>
                          支持所见即所得，简便、直观、动态交互，数模联动等3D前端交互技术，对传统数字化系统进行升级；
                        </p>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="col-lg-6 col-md-12 col-sm-12 col-12">
                  <div className="feature feature--boxed-border feature--bg-1">
                    <div className="icon-block icon-block--style-1-v1">
                      <div className="block-icon">
                        <i className="icon-number">2</i>
                      </div>
                      <div className="block-content">
                        <h3 className="heading heading-5 strong-500  mb-2">设备故障一键保修</h3>
                        <p className="mb-2">
                          当巡检人员发现设备故障，打开手机扫描二维码，方便快捷一键报修，后台实时获取设备定位、设备运行参数、设备历史维修记录等信息，系统自己指派距离
                          最近的空闲维修人员前往修复，使设备维修耗时大大降低
                        </p>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div className="row">
                <div className="col-lg-6 col-md-12 col-sm-12 col-12">
                  <div className="feature feature--boxed-border feature--bg-1">
                    <div className="icon-block icon-block--style-1-v1">
                      <div className="block-icon">
                        <i className="icon-number">3</i>
                      </div>
                      <div className="block-content">
                        <h3 className="heading heading-5 strong-500 mb-2">设备能耗实时监测，优化管理</h3>
                        <p className="mb-2">
                          对水位数据、流量数据、水压数据、水质数据、地理数据、视频监控、泵站数据进行采集，
                          可对时间、空间、地理等维度的数据进行划分；
                        </p>
                        <p className="mb-2">
                          支持能耗预测及警报；
                        </p>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="col-lg-6 col-md-12 col-sm-12 col-12">
                  <div className="feature feature--boxed-border feature--bg-1">
                    <div className="icon-block icon-block--style-1-v1">
                      <div className="block-icon">
                        <i className="icon-number">4</i>
                      </div>
                      <div className="block-content">
                        <h3 className="heading heading-5 strong-500 mb-2">完备的设备规格库</h3>
                        <p className="mb-2">
                          水质监测设备的规格品类繁多，更新迭代快速，通过建立与厂商的合作渠道，对设备的规格进行整合，方便业主进行选择
                        </p>
                        <p className="mb-2">
                          通过厂商提供的设备信息及规格数据，通过模拟测试的结果为业主进行专项方案打包服务，为双方合作打通渠道。
                        </p>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div className="row">
                <div className="col-lg-6 col-md-12 col-sm-12 col-12">
                  <div className="feature feature--boxed-border feature--bg-1">
                    <div className="icon-block icon-block--style-1-v1">
                      <div className="block-icon">
                        <i className="icon-number">5</i>
                      </div>
                      <div className="block-content">
                        <h3 className="heading heading-5 strong-500 mb-2">集成化数据中心，大屏集中展示</h3>
                        <p className="mb-2">
                          提供水务多个层面、多场景，从整体到个体、从分类到对象、从里到外的数据管控中心，支持大屏投放及分屏展示，支持可配置化，支持目标对象模型交互展示；帮助业主实时做出管理决策，帮助楼宇更健康、更高效的运营；
                        </p>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="col-lg-6 col-md-12 col-sm-12 col-12">
                  <div className="feature feature--boxed-border feature--bg-1">
                    <div className="icon-block icon-block--style-1-v1">
                      <div className="block-icon">
                        <i className="icon-number">6</i>
                      </div>
                      <div className="block-content">
                        <h3 className="heading heading-5 strong-500 mb-2">智能高效的应急事件处理</h3>
                        <p className="mb-2">
                          对洪涝灾害事件进行标准化管理，例如日常情况下，设置洪水溢流路径，通过对安全通道上的设备进行监控，自动监测路径是否畅通，并可按路径进行应急疏散模拟；
                        </p>
                        <p>
                          支持多事件联动、多状态预警、多设备配合等突发应急事件处理
                        </p>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      );
    }
  };


