import React from 'react';

class solveWays extends React.Component {
    render() {
        return <div>
            <section style={{backgroundColor:'rgb(38,86,121) !important'}} className="slice slice--arrow bg-base-1 b-xs-top ">
                <div className="sct-inner container">
                    <div className="section-title section-title-inverse section-title--style-1 text-center">
                        <h3 className="section-title-inner">
                            <span>智慧方案库</span>
                        </h3>
                        {/* <span class="section-title-delimiter clearfix d-none"></span> */}
                    </div>
                    {/* <div class="fluid-paragraph fluid-paragraph-sm strong-300 text-center">
                              Lorem ipsum dolor sit amet, consectetuer adipiscing elit. Praesent vestibulum
                              molestie lacus Aenean nonummy hendrerit mauris. Phasellus porta fusce suscipit
                              varius mi.
                          </div> */}
                </div>
            </section>
            <section className="slice-xl sct-color-1">
                <div className="container">
                    <div className="row-wrapper">
                        <div className="row">
                            <div className="col-lg-4 col-md-12 col-sm-12 col-12">
                                <div className="block block--style-3">
                                    <div className="block-body">
                                        <div className="icon-block--style-1-v5 text-center">
                                            <div className=" c-gray-light">
                                                <img alt="img" src={require("@/assets/images/wise1.jpg")} />
                                            </div>
                                            <div className="block-content mt-5">
                                                <h3 className="heading heading-6 strong-600 text-normal">
                                                    降雨模拟
                              </h3>
                                                <p className="mb-4" style={{ paddingLeft: '10px', paddingRight: '10px' }}>
                                                通过暴雨洪水公式，结合地区参数，模拟出特定重现期特定降雨历时的降雨强度与时间关系图。
                              </p>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="col-lg-4 col-md-12 col-sm-12 col-12">
                                <div className="block block--style-3">
                                    <div className="block-body">
                                        <div className="icon-block--style-1-v5 text-center">
                                            <div className=" c-gray-light">
                                                <img alt="img" src={require("@/assets/images/wise2.jpg")} />
                                            </div>
                                            <div className="block-content mt-5">
                                                <h3 className="heading heading-6 strong-600 text-normal">
                                                    地表积水
                              </h3>
                                                <p className="mb-4" style={{ paddingLeft: '10px', paddingRight: '10px' }}>
                                                    通过模拟出的暴雨强度，结合当地地表高程和地表径流能力模拟出该地区积水深度以及与时间的变化关系。
                              </p>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="col-lg-4 col-md-12 col-sm-12 col-12">
                                <div className="block block--style-3">
                                    <div className="block-body">
                                        <div className="icon-block--style-1-v5 text-center">
                                            <div className=" c-gray-light ">
                                                <img alt="img" src={require("@/assets/images/wise3.jpg")}/>
                                            </div>
                                            <div className="block-content mt-5">
                                                <h3 className="heading heading-6 strong-600 text-normal">
                                                    暴雨预警
                              </h3>
                                                <p className="mb-4">
                                                    当地表降雨深度到达一定深度时，平台提供预警给决策者。
                              </p>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        </div>

    }
}

export default solveWays;


