import React from "react";
import { Link } from "react-router-dom";

class companyNews extends React.Component {
  render() {
    const companyNews = [
      {
        id: 0,
        catagory: "公司要闻",
        title: "上海枢汇智能科技有限公司成立",
        introduce:
          "2018年6月1日，上海枢汇智能科技有限公司成立正式成立，专注于智能水务...",
        img: "news2.jpg"
      },
      {
        id: 1,
        catagory: "产品发布",
        title: "水务平台产品发布",
        introduce:
          "时值金秋之际，上海枢汇智能科技有限公司旗下水务产品盛大发布。",
        img: "news1.jpg"
      }
    ];

    const industryNews = [
      {
        id: 1000,
        catagory: "行业新闻",
        title:
          "中国水务集团与仁化县政府签署 仁化县城乡供水公共安全合作框架协议",
        introduce:
          "  2019年4月18日，中国水务集团与仁化县人民政府关于城乡供水一体化合作框架协议签约仪式在仁化县政府礼堂会议室举行。",
        img: "img-1.jpg"
      },
      {
        id: 1001,
        catagory: "行业新闻",
        title: "中国水务集团斥资12亿港元收购康达环保29.52%股权",
        introduce:
          " 2019年4月3日，中国水务集团有限公司与康达国际环保有限公司签订协议，以12亿港元收购其29.52%股权。",
        img: "img-2.jpg"
      },
      {
        id: 1002,
        catagory: "行业新闻",
        title: "中国水务集团及旗下子公司荣获2018年度水业企业评选三大奖项",
        introduce:
          " 2019年3月23日，在E20环境平台举办的第十七届水业战略论坛上，2018年度水业企业评选获奖榜单隆重揭晓。",
        img: "img-5.jpg"
      }
    ];

    return (
      <div>
        <section className="slice sct-color-2 relative b-xs-top ">
          <div className="container">
            <div className="section-title section-title--style-1 text-center mb-2">
              <h3 className="section-title-inner text-normal">
                <span>公司新闻</span>
              </h3>
              <span className="section-title-delimiter clearfix d-none" />
            </div>
            <span className="clearfix" />
            <span className="space-xs-xl" />
            <div className="row-wrapper">
              <div className="row">
                {companyNews.map((v, i) => {
                  return (
                    <div key={i} className="col-lg-6 col-md-12 col-sm-12 col-12">
                      <div className="block block--style-1 list">
                        <div className="block-image">
                          <div>
                            <img alt="img"
                              src={require(`@/assets/images/news/${v.img}`)}
                            />
                          </div>
                        </div>
                        <div className="block-content">
                          <div className="block-body py-1">
                            <small>{v.catagory}</small>
                            <h3 className="heading heading-5 strong-500">
                              <Link to={"/newsdetails/" + v.id}>{v.title}</Link>
                            </h3>
                            <p
                              className="mt-3 c-gray-light"
                              style={{ paddingRight: "20px" }}
                            >
                              {v.introduce}
                            </p>
                          </div>
                        </div>
                      </div>
                    </div>
                  );
                })}
              </div>
            </div>
          </div>
        </section>
        <section
          className="slice sct-color-2 relative b-xs-bottom"
          style={{ paddingTop: "0px" }}
        >
          <div className="container">
            <div className="section-title section-title--style-1 text-center">
              <h3 className="section-title-inner">
                <span>行业新闻</span>
              </h3>
              <span className="section-title-delimiter clearfix d-none" />
            </div>
            <span className="clearfix" />
            <div className="row cols-xs-space cols-sm-space cols-md-space">
              {industryNews.map((v, i) => {
                return <div key={i} className="col-lg-4">
                  <div className="block block--style-3">
                    <div className="block-image relative">
                      <div className="view view-first">
                        <Link to={'/newsdetails/'+v.id}>
                          <img alt="img"
                            src={require(`@/assets/images/news/${v.img}`)}
                          />
                        </Link>
                      </div>
                    </div>
                    <div className="block-body">
                      <h3 className="heading heading-6 strong-600">
                        <Link to={'/newsdetails/'+v.id}>
                          {v.title}
                        </Link>
                      </h3>
                      <p>
                        {v.introduce}
                      </p>
                      <a
                        href="news3.html"
                        target="_blank"
                        className="link link-sm link--style-2 mt-3"
                      >
                        <i className="fa fa-long-arrow-right" /> 了解更多
                      </a>
                    </div>
                  </div>
                </div>;
              })}
            </div>
          </div>
        </section>
      </div>
    );
  }
}

export default companyNews;



