import React from 'react';

class productSchema extends React.Component {
    render() {
        return <section className="slice sct-color-2 relative b-xs-top ">
            <div className="container">
                <div className="section-title section-title--style-1 text-center mb-2">
                    <h3 className="section-title-inner text-normal">
                        <span>产品架构</span>
                    </h3>
                    <span className="section-title-delimiter clearfix d-none" />
                </div>
                <span className="clearfix" />
                <span className="space-xs-xl" />
                <div className="row align-items-center cols-xs-space cols-sm-space cols-md-space">
                    <div className="col-lg-6">
                        <div className="block block-image">
                            <img alt="img" src={require("@/assets/images/prv/img-device-4.png")} className="img-responsive" />
                        </div>
                    </div>
                    <div className="col-lg-6">
                        <div>
                            <div className="icon-block icon-block--style-1-v3 icon-block--style-1-v3-dashed">
                                <div className="block-icon">
                                    <i className="iconfont">&#xe620;</i>
                                </div>
                                <div className="block-content">
                                    <h3 className="heading heading-6 strong-600">
                                        统一门户
                                    </h3>
                                    <p>
                                        外网、内网、手机端
                                    </p>
                                </div>
                            </div>
                            <span className="space-xs-md" />
                            <div className="icon-block icon-block--style-1-v3 icon-block--style-1-v3-dashed">
                                <div className="block-icon">
                                <i className="iconfont">&#xe620;</i>
                                </div>
                                <div className="block-content">
                                    <h3 className="heading heading-6 strong-600">
                                        应用层
                                    </h3>
                                    <p>
                                        在线监测、预警信息、数据分析、地图GIS、模型分析、智慧运维
                                    </p>
                                </div>
                            </div>
                            <span className="space-xs-md" />
                            <div className="icon-block icon-block--style-1-v3 icon-block--style-1-v3-dashed">
                                <div className="block-icon">
                                <i className="iconfont">&#xe620;</i>
                                </div>
                                <div className="block-content">
                                    <h3 className="heading heading-6 strong-600">
                                        平台层
                                    </h3>
                                    <p>
                                        统一数据监控平台、统一数据交换平台、统一数据服务平台、统一GIS服务平台、模型管理平台
                                    </p>
                                </div>
                            </div>
                            <span className="space-xs-md" />
                            <div className="icon-block icon-block--style-1-v3 icon-block--style-1-v3-dashed">
                                <div className="block-icon">
                                <i className="iconfont">&#xe620;</i>
                                </div>
                                <div className="block-content">
                                    <h3 className="heading heading-6 strong-600">
                                        传输层
                                    </h3>
                                    <p>
                                        4G网络、DTU、WIFI、有线网、公共以太网
                                    </p>
                                </div>
                            </div>
                            <span className="space-xs-md" />
                            <div className="icon-block icon-block--style-1-v3 icon-block--style-1-v3-dashed">
                                <div className="block-icon">
                                <i className="iconfont">&#xe620;</i>
                                </div>
                                <div className="block-content">
                                    <h3 className="heading heading-6 strong-600">
                                        感知层
                                    </h3>
                                    <p>
                                        水位数据、流量数据、水压数据、水质数据、地理数据、视频监控、泵站数据
                                    </p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <span className="space-xs-xl" />
                <span className="clearfix" />
                <div className="fluid-paragraph c-gray-light strong-300 text-center" style={{ lineHeight: 2 }}>
                    以数据管理为基础，为业主提供与水务耦合度高，多业态、可定制、自由化的“智慧水务”
                    业务增值服务；定位精准、多方案支持、多渠道协助，解决业主定制化的智慧水务运营需求，提高业主的产品品牌及独具特色的“智慧社区（城市）”服务。
                </div>
                <span className="space-xs-xl" />
                <span className="clearfix" />
                <div className="row">
                    <div className="col-lg-2">
                    </div>
                    <div className="col-lg-2">
                        <div className="icon-block icon-block--style-1-v3 icon-block--style-1-v3-dashed">
                            <img alt="img" src={require("@/assets/images/t1.jpg")} style={{ width: '80px' }} />
                            <div style={{ color: '#666', marginLeft: '28px', marginTop: '10px' }}>GIS</div>
                            {/* <div class="block-icon">
                                                <i class="fa fa-gears"></i>
                                            </div> */}
                        </div>
                    </div>
                    <div className="col-lg-2">
                        <div className="icon-block icon-block--style-1-v3 icon-block--style-1-v3-dashed">
                            <img alt="img" src={require("@/assets/images/t2.jpg")} style={{ width: '80px' }} />
                            <div style={{ color: '#666', marginLeft: '15px', marginTop: '10px' }}>SWMM</div>
                            {/* <div class="block-icon">
                                                <i class="fa fa-gears"></i>
                                            </div> */}
                        </div>
                    </div>
                    <div className="col-lg-2">
                        <div className="icon-block icon-block--style-1-v3 icon-block--style-1-v3-dashed">
                            <img alt="img" src={require("@/assets/images/t3.jpg")} style={{ width: '80px' }} />
                            <div style={{ color: '#666', marginLeft: '16px', marginTop: '10px' }}>大数据</div>
                            {/* <div class="block-icon">
                                                <i class="fa fa-gears"></i>
                                            </div> */}
                        </div>
                    </div>
                    <div className="col-lg-2">
                        <div className="icon-block icon-block--style-1-v3 icon-block--style-1-v3-dashed">
                            <img alt="img" src={require("@/assets/images/t4.jpg")} style={{ width: '80px' }} />
                            <div style={{ color: '#666', marginLeft: '16px', marginTop: '10px' }}>云计算</div>
                            {/* <div class="block-icon">
                                                <i class="fa fa-gears"></i>
                                            </div> */}
                        </div>
                    </div>
                    <div className="col-lg-2">
                        <div className="icon-block icon-block--style-1-v3 icon-block--style-1-v3-dashed">
                            <img alt="img" src={require("@/assets/images/t5.jpg")} style={{ width: '80px' }} />
                            <div style={{ color: '#666', marginLeft: '10px', marginTop: '10px' }}>人工智能</div>
                            {/* <div class="block-icon">
                                                <i class="fa fa-gears"></i>
                                            </div> */}
                        </div>
                    </div>
                </div>
                <span className="clearfix" />
                <span className="space-xs-xl" />
            </div>
        </section>
    }
}

export default productSchema;


