            "<br/>在华建数创成立伊始，即与盛裕集团签署战略合作协议，经过一年的认真磋商、积极筹措，双方共同出资成立华建盛裕（上海）建筑科技有限公司。华建盛裕将结合华建集团强大的BIM能力/和盛裕集团国际领先的FM理念与经验，共同打造“华建盛裕智慧运维管理系统”，致力探索BIM在运维阶段的作用和价值，打造FM产品链，构筑BIM+FM生态圈，探索智慧城市的建设与发展，并积极拓展中国与海外市场。",
